$(document).ready(function() {

    // Burger
    $('.header__burger').click(function () {
        $(this).toggleClass('header__burger--active')
        $('.header__menu-inner').toggleClass('header__menu-inner--active')
    })

    // Menu
    $(".header__item-show").click((function(){$(this).toggleClass("header__item-show--active")
        $(this).siblings(".header__item-hidden").slideToggle()}));

    // modal

    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }


})